import * as React from 'react'
import { Link, type HeadFC, type PageProps } from 'gatsby';

import { Button, Container, Image } from 'react-bootstrap';

import Navigation from '../components/navigation';
import Footer from '../components/footer';

import crochet1Image from '../images/crochet-1.png';
import crochetBagsImg from '../images/crochet-bags.png';
import crochetBasketsImg from '../images/crochet-baskets.png';
import crochetPillowImg from '../images/crochet-pillow.png';
import crochetShowcaseImage from '../images/crochet-showcase.png';

import './crochet.css';
import LeftLargeImageCard from '../components/left-large-image-card';
import ViewOnEtsy from '../components/view-on-etsy';
import ShowCase from '../components/show-case';


const CrochetPage: React.FC<PageProps> = () => {
  return (
    <>
      <Navigation />
      <main>
        <div className='d-flex py-5'>
          <Container className='flex-fill d-flex my-story-container'>
            <div className='my-story flex-fill'>
              <div className='row'>
                <div className='col-12 col-lg-5 p-5'>
                  <h1>Crochet</h1>
                  <div>
                    <p className='p-2'>Each crochet piece is made with care and creativity, adding warmth and charm to your home and daily life.</p>
                    <p className='p-2'>From soft baskets to cozy pillows, every stitch tells a story — and no two items are ever alike.</p>
                    <div className='p-2 d-flex justify-content-between'>
                      <div className='flex-grow-1'>
                        <p>Explore more one-of-a-kind Crochets and new designs in our Etsy shop.</p>
                      </div>
                      <Link className='p-2' to='https://www.etsy.com/shop/LOVE2CRAFTLLC'>
                        <Button variant="outline-dark text-nowrap">Our Store</Button>
                      </Link>
                    </div>
                  </div>

                  <Image className='py-5' src={crochet1Image} fluid />
                </div>

                <div className='col-12 col-lg-7'>
                  <ShowCase showcaseImage={crochetShowcaseImage} />
                </div>
              </div>
            </div>
          </Container>
        </div >
        <div className="d-flex justify-content-start flex-wrap px-2 px-md-5">
          <div className='p-4 align-items-stretch col-12 col-xl-6 col-xxl-4'>
            <LeftLargeImageCard
              link="https://www.etsy.com/shop/LOVE2CRAFTLLC?section_id=48279564"
              linkText="Etsy link"
              image={crochetBasketsImg}
              title="Crochet Baskets"
              description={
                <div>
                  <p>Our crochet baskets bring beauty and function together. It's perfect for organizing, decorating, or gifting.</p>
                  <p>Each basket is made by hand with soft, durable yarn, giving every piece its own unique personality.</p>
                </div>
              }>
            </LeftLargeImageCard>
          </div>
          <div className='p-4 align-items-stretch col-12 col-xl-6 col-xxl-4'>
            <LeftLargeImageCard
              link="https://www.etsy.com/shop/LOVE2CRAFTLLC?section_id=50536699"
              linkText="Etsy link"
              image={crochetBagsImg}
              title="Crochet Bags"
              description={
                <div>
                  <p>Each crochet bag is thoughtfully designed to blend comfort and creativity.</p>
                  <p>Whether for everyday use or special outings, these handmade bags are stylish, sturdy, and one-of-a-kind.</p>
                </div>
              }>
            </LeftLargeImageCard>
          </div>
          <div className='p-4 align-items-stretch col-12 col-xl-6 col-xxl-4'>
            <LeftLargeImageCard
              link="https://www.etsy.com/shop/LOVE2CRAFTLLC?section_id=50536709"
              linkText="Etsy link"
              image={crochetPillowImg}
              title="Crochet Pillow"
              description={
                <div>
                  <p>Our crochet pillows are crafted to add warmth and texture to any room.</p>
                  <p>Each one is soft, charming, and completely unique — perfect for bringing handmade comfort into your home.</p>
                </div>
              }>
            </LeftLargeImageCard>
          </div>
        </div>
      </main >
      <ViewOnEtsy category='Crochets' />
      <Footer />
    </>
  )
}

export default CrochetPage

export const Head: HeadFC = () => <title>Crochets</title>